/**
 * 
 * @unfile
 * @module UE.browser
 */

var browser  = function(){
    var agent = navigator.userAgent.toLowerCase(),
        opera = window.opera,
        browser = {
            /**
             * @property {boolean} ie IE
             * @example
             * ```javascript
             * if ( UE.browser.ie ) {
         *     console.log( 'IE' );
         * }
             * ```
             */
            ie		:  /(msie\s|trident.*rv:)([\w.]+)/.test(agent),

            /**
             * @property {boolean} opera Opera
             * @example
             * ```javascript
             * if ( UE.browser.opera ) {
         *     console.log( 'Opera' );
         * }
             * ```
             */
            opera	: ( !!opera && opera.version ),

            /**
             * @property {boolean} webkit webkit
             * @example
             * ```javascript
             * if ( UE.browser.webkit ) {
         *     console.log( 'webkit' );
         * }
             * ```
             */
            webkit	: ( agent.indexOf( ' applewebkit/' ) > -1 ),

            /**
             * @property {boolean} mac mac
             * @example
             * ```javascript
             * if ( UE.browser.mac ) {
         *     console.log( 'mac' );
         * }
             * ```
             */
            mac	: ( agent.indexOf( 'macintosh' ) > -1 ),

            /**
             * @property {boolean} quirks 
             * @example
             * ```javascript
             * if ( UE.browser.quirks ) {
         *     console.log( '' );
         * }
             * ```
             */
            quirks : ( document.compatMode == 'BackCompat' )
        };

    /**
     * @property {boolean} gecko gecko
     * @example
     * ```javascript
     * if ( UE.browser.gecko ) {
    *     console.log( 'gecko' );
    * }
     * ```
     */
    browser.gecko =( navigator.product == 'Gecko' && !browser.webkit && !browser.opera && !browser.ie);

    var version = 0;

    // Internet Explorer 6.0+
    if ( browser.ie ){


        var v1 =  agent.match(/(?:msie\s([\w.]+))/);
        var v2 = agent.match(/(?:trident.*rv:([\w.]+))/);
        if(v1 && v2 && v1[1] && v2[1]){
            version = Math.max(v1[1]*1,v2[1]*1);
        }else if(v1 && v1[1]){
            version = v1[1]*1;
        }else if(v2 && v2[1]){
            version = v2[1]*1;
        }else{
            version = 0;
        }

        browser.ie11Compat = document.documentMode == 11;
        /**
         * @property { boolean } ie9Compat  IE9 
         * @warning IE undefined
         * @example
         * ```javascript
         * if ( UE.browser.ie9Compat ) {
         *     console.log( 'IE9' );
         * }
         * ```
         */
        browser.ie9Compat = document.documentMode == 9;

        /**
         * @property { boolean } ie8 IE8
         * @warning IE undefined
         * @example
         * ```javascript
         * if ( UE.browser.ie8 ) {
         *     console.log( 'IE8' );
         * }
         * ```
         */
        browser.ie8 = !!document.documentMode;

        /**
         * @property { boolean } ie8Compat  IE8 
         * @warning IE undefined
         * @example
         * ```javascript
         * if ( UE.browser.ie8Compat ) {
         *     console.log( 'IE8' );
         * }
         * ```
         */
        browser.ie8Compat = document.documentMode == 8;

        /**
         * @property { boolean } ie7Compat  IE7 
         * @warning IE undefined
         * @example
         * ```javascript
         * if ( UE.browser.ie7Compat ) {
         *     console.log( 'IE7' );
         * }
         * ```
         */
        browser.ie7Compat = ( ( version == 7 && !document.documentMode )
            || document.documentMode == 7 );

        /**
         * @property { boolean } ie6Compat  IE6  
         * @warning IE undefined
         * @example
         * ```javascript
         * if ( UE.browser.ie6Compat ) {
         *     console.log( 'IE6' );
         * }
         * ```
         */
        browser.ie6Compat = ( version < 7 || browser.quirks );

        browser.ie9above = version > 8;

        browser.ie9below = version < 9;

    }

    // Gecko.
    // if ( browser.gecko ){
    //     var geckoRelease = agent.match( /rv:([\d\.]+)/ );
    //     if ( geckoRelease )
    //     {
    //         geckoRelease = geckoRelease[1].split( '.' );
    //         version = geckoRelease[0] * 10000 + ( geckoRelease[1] || 0 ) * 100 + ( geckoRelease[2] || 0 ) * 1;
    //     }
    // }

    /**
     * @property { Number } chrome Chrome, Chrome
     * @warning chrome undefined
     * @example
     * ```javascript
     * if ( UE.browser.chrome ) {
     *     console.log( 'Chrome' );
     * }
     * ```
     */
    if (/chrome\/(\d+\.\d)/i.test(agent)) {
        browser.chrome = + RegExp['\x241'];
    }

    /**
     * @property { Number } safari Safari, Safari
     * @warning safari undefined
     * @example
     * ```javascript
     * if ( UE.browser.safari ) {
     *     console.log( 'Safari' );
     * }
     * ```
     */
    if(/(\d+\.\d)?(?:\.\d)?\s+safari\/?(\d+\.\d+)?/i.test(agent) && !/chrome/i.test(agent)){
        browser.safari = + (RegExp['\x241'] || RegExp['\x242']);
    }


    // Opera 9.50+
    if ( browser.opera )
        version = parseFloat( opera.version() );

    // WebKit 522+ (Safari 3+)
    if ( browser.webkit )
        version = parseFloat( agent.match( / applewebkit\/(\d+)/ )[1] );

    /**
     * @property { Number } version 
     * @remind
     * <ul>
     *     <li>IE5,6,7,8,9,10</li>
     *     <li>gecko10900158900</li>
     *     <li>webkitbuild ( 522)</li>
     * </ul>
     * @example
     * ```javascript
     * console.log( ' ' + UE.browser.version );
     * ```
     */
    browser.version = version;

    /**
     * @property { boolean } isCompatible UEditor
     * @example
     * ```javascript
     * if ( UE.browser.isCompatible ) {
     *     console.log( 'UEditor' );
     * }
     * ```
     */
    browser.isCompatible =
        !browser.mobile && (
            ( browser.ie && version >= 6 ) ||
                ( browser.gecko && version >= 10801 ) ||
                ( browser.opera && version >= 9.5 ) ||
                ( browser.air && version >= 1 ) ||
                ( browser.webkit && version >= 522 ) ||
                false );
    return browser;
};

export default browser;

